import { Controller, type Path, useFormContext } from "react-hook-form";
import BaseCheckbox from "../../vendor/Checkbox.jsx";

export type CheckboxProps = {
  /**
   * If `true`, the component is disabled.
   *
   * @default false
   */
  disabled?: boolean | undefined;

  /**
   * An error message to display below the element when in an invalid state.
   */
  error?: { message?: string | undefined } | undefined;

  /**
   * Explanatory text to render below the element.
   */
  helpText?: React.ReactNode | undefined;

  /**
   * A user-friendly label displayed alongside the element.
   */
  label: string;

  /**
   * A unique name identifying the element within a form.
   */
  name: string;

  /**
   * When `true`, the element will be invalid when touched and unchecked.
   *
   * @default false
   */
  required?: boolean | undefined;

  /**
   * A boolean indicating whether the element is in a selected state.
   *
   * @default false
   */
  value?: boolean | undefined;

  /**
   * @internal
   */
  [key: string]: unknown;
};

/**
 * Checkboxes give users a binary choice from a set of options.
 *
 * <!-- TODO(ndhoule): Add a screenshot here -->
 *
 * @param props
 * @see {@link Form}
 * @example
 * ```ts
 * import { Checkbox } from "@netlify/sdk/react/components";
 *
 * <Checkbox />
 * ```
 */
export const Checkbox = <FieldValues extends Record<string, unknown>>({
  // Ignore this value when passed so it doesn't end up in the `otherProps` rest
  value: _value,

  name,
  label,
  helpText,
  error,
  required,
  ...otherProps
}: CheckboxProps) => {
  const { control } = useFormContext<FieldValues>();

  return (
    <Controller
      name={name as unknown as Path<FieldValues>}
      control={control}
      render={({ field, fieldState }) => {
        return (
          <BaseCheckbox
            label={label}
            controlledChecked={Boolean(field.value)}
            helpText={helpText}
            error={error?.message ?? fieldState.error?.message}
            required={required}
            {...otherProps}
            {...field}
            value={field.value as string}
          />
        );
      }}
    />
  );
};
